import React, {Component} from 'react';
import DatePicker from 'react-bootstrap-date-picker';
import { render } from 'react-dom';
import Select from 'react-select-plus';
import {Modal, Panel, PanelGroup} from 'react-bootstrap';
import {userDetail, editProfileUser,uploadImage,uploadCV} from '../../actions/UserProfileAction';
import moment from 'moment';
import axios from "axios";
import UserStore from '../../stores/UserStore';
import 'react-select-plus/dist/react-select-plus.css';

class UserPopup extends Component {

    constructor() {
        super();
        this.state = {
            options: this.options,
            showPopup: false,
            isLoading: false,
            editMode: false,
            form:{
                username: "",
                email: "",
                firstName: "",
                lastName: "",
                sinceDate: null,
                photo:"",
                job:"",
                skypeId:"",
                country:"",
                linkedin:"",
                cv:"",
                skills: [{name:"",ratio:null}],
                projects: [{ name: "", role: "" }],
                availability:{hoursWork:"", infos:"", timeZone:"", vocations:""},
            },
            filename:"",
            file:{name:'',},
            filenameCV:"",
            fileCV:{name:'',type:''},
            name:'',
            formError: {
                status: false,
                message: ""
            },
            userMsg:null,
            statusCV:null,
            error: {
                status: false,
                message: ""
            },
            value:'',

        };
        this.mandatoryFieldsList = [];
        this.onChangeCV = this.onChangeCV.bind(this);

    }
    componentWillMount() {
        UserStore.bindUpdateHandler(this.userDetailOnUpdate.bind(this));

    }
    componentWillUnmount() {
        UserStore.bindUpdateHandler(this.userDetailOnUpdate.bind(this));
    }

    componentDidMount(){
    	this.setDataFormData();
        this.edit_user();
        uploadCV();
    }

    shouldComponentUpdate() {
        this.state.statusCV == 'upload_cv_success'
        return true;
    }

    userDetailOnUpdate(response) {
        console.log("user On Update");
        console.log(response);
        const {result, message} = response

        if( message == "upload_cv_success") {
            this.setState({
                statusCV: message
            });
        }
        this.state;
    }

    changeContent(name, e) {
        var state = this.state;
        state['form'][name] = e.target.value;
        this.setState(state);
    }

    validateMandatoryFields(){
        var mandatories = [];
        var formValues = this.state.form;
        this.mandatoryFieldsList.map(function (field) {
            if (formValues[field] != null) {
                if (formValues[field].trim().length == 0) {
                    mandatories.push(field);
                }
            }else{
                mandatories.push(field);
            }

        });
        var state = this.state;
        state['mandatoryFields'] = mandatories.slice();
        this.setState(state);

        if (mandatories.length > 0) {
            return false;
        } else {
            return true;
        }
    }

    setCountry(name,val) {
        if(val != null){
        var state = this.state;
        state['form']['country'] = val.value;
        this.setState(state);
        }
    }

    changeDate(name, value) {
        if (value != null && name !=null) {
            var dateValue = value.substr(0, 10);
        } else {
            var dateValue = null;
        }

        var state = this.state;
        state['form'][name] = dateValue;
        this.setState(state);
    }

    convertDate(date){
        if (date != null && date != 0) {
            var date_ = date.substr(0, 10).replace(/\//g , "-");
            var dateArray = date_.split("-");
            date = dateArray[2]+"-"+dateArray[1]+"-"+dateArray[0]+' '+date.substr(11);
        return date;
        }
    }

    edit_user(event) {
        event.preventDefault();
        if(this.validateUploadCV()) {
            if (this.state.statusCV == 'upload_cv_success') {
                var filenameCV = this.state.fileCV.name;
            }
            this.onFormSubmitCV();

            var updateType = "form";
            const {form} = this.state;
            var userData = {
                "email": form.email,
                "username": form.username,
                "lastName": form.lastName,
                "sinceDate": form.sinceDate,
                "firstName": form.firstName,
                "job": form.job,
                "skypeId": form.skypeId,
                "country": form.country,
                "linkedin": form.linkedin,
                "cv": filenameCV
            };
            editProfileUser(userData);
        }
    }

    // UPLOAD FILE CV

    onFormSubmitCV() {
        this.fileUploadCV(this.state.fileCV);
    }

    onChangeCV(e) {
        this.setState({ fileCV: e.target.files[0]});
    }

    fileUploadCV(fileCV) {
        if(this.state.fileCV != null){
        var filenameCV=this.state.fileCV.name;
        var formData = new FormData();
        var username=this.state.form.username;
        formData.append('uploadedFile',fileCV);
        formData.append('filename',filenameCV);
        uploadCV(formData,username);
        }
    }

    handleChangeFile(event) {
        const fileCV = event.target.files[0];
        if(fileCV!= null){
        var filenameCV=fileCV.name;
        }
        let formData = new FormData();
        formData.append('uploadedFile',fileCV);
        formData.append('filename',filenameCV);
        if(this.validateUploadCV()){
        uploadCV(formData);
        }
        this.setState({ fileCV: event.target.files[0]});
    }

    setDataFormData() {
        const {userInfo} = this.props;
        if (userInfo != null) {
            var state = this.state;
            state['form']['email'] = userInfo.email;
            state['form']['username'] = userInfo.username;
            state['form']['lastName'] = userInfo.lastName;
            state['form']['firstName'] = userInfo.firstName;
            state['form']['sinceDate'] = userInfo.sinceDate!=null? this.convertDate(userInfo.sinceDate):null;
            state['form']['job'] = userInfo.job;
            state['form']['skypeId'] = userInfo.skypeId;
            state['form']['country'] = userInfo.country;
            state['form']['linkedin'] = userInfo.linkedin;
            state['cv'] = userInfo.cv;
            this.setState(state);
        }
    }

    validateUploadCV(){
        if (this.state.fileCV.type == "application/msword" || this.state.fileCV.type =="" || this.state.fileCV.type == "application/pdf") {
            var state = this.state;
            state['error']["status"] = false;
            state['error']["message"] = "";
            this.setState(state);
            return true;
        }
        else{
            var state = this.state;
            state['error']["status"] = true;
            state['error']["message"] = "Your file format is not good";
            this.setState(state);
            return false;
        }

    }

        closePopup() {
        if(this.validateUploadCV()){
            if(this.state.message == 'upload_cv_success'){}
            setTimeout(() => {
                this.props.closePopup();
            }, 100);
        }
    }


    renderPopup() {
        const {all_countries} = this.props.baseData;
        return (
				<div className='popup_skill'>
					<div className="close_bio" onClick={this.props.closePopup}></div>
					<form method="get" onSubmit={this.edit_user.bind(this)} >
						<div className="header_bio">EDIT PROFILE</div>
						<table className="edit_profile">
                            <tbody>
							<tr>
								<td><label className="label_profile"><span>First name: </span></label></td>
								<td><input
                                            className="ip_profile"
										   value={this.state.form.firstName}
										   onChange={this.changeContent.bind(this, "firstName")}
                                           type="text"
								/></td>
							</tr>
                            </tbody>
                            <tbody>
							<tr>
								<td><label className="label_profile"><span>Last name: </span></label></td>
								<td><input
                                        className="ip_profile"
										   value={this.state.form.lastName}
										   onChange={this.changeContent.bind(this, "lastName")}
                                           type="text"
								/></td>
							</tr>
                            </tbody>
                            <tbody>
                            <tr>
                                <td><label className="label_profile"><span>Manaty member since: </span></label></td>
                                <td><span className="ip_picker">
                                    <DatePicker className="picker"
                                                dateFormat="DD/MM/YYYY"
                                                value={this.state.form.sinceDate}
                                                onChange={this.changeDate.bind(this, "sinceDate")}
                                /></span></td>
                            </tr>
                            </tbody>
                            <tbody>

							<tr>
								<td><label className="label_profile"><span>Job:  </span></label></td>
								<td><input
                                    className="ip_profile"
                                    value={this.state.form.job}
                                    onChange={this.changeContent.bind(this, "job")}
                                    type="text"/></td>
							</tr>
                            </tbody>

                            <tbody>

							<tr>
								<td><label className="label_profile"><span >Country:  </span></label></td>
								<td className='coutry'>
                                    <div><Select
                                        className="select_popup_country"
                                        value={this.state.form.country}
                                        options={this.props.countryAll}
                                        onChange={this.setCountry.bind(this, 'value')}
                                    /></div>
                                </td>
							</tr>
                            </tbody>

                            <tbody>

							<tr>
								<td><label className="label_profile"><span>Manaty email: </span></label></td>
								<td><input
                                            className="ip_profile"
                                            value={this.state.form.email}
                                           onChange={this.changeContent.bind(this, "email")}
                                           type="text"
                                /></td>
							</tr>
                            </tbody>
                            <tbody>

							<tr>
								<td><label className="label_profile"><span>Skype id: </span></label></td>
								<td><input
                                    className="ip_profile"
                                    value={this.state.form.skypeId}
                                    onChange={this.changeContent.bind(this, "skypeId")}
                                    type="text" /></td>
							</tr>
                            </tbody>
                            <tbody>

							<tr>
								<td><label className="label_profile"><span>Linkedin link: </span></label></td>
								<td><input
                                    className="ip_profile"
                                    value={this.state.form.linkedin}
                                    onChange={this.changeContent.bind(this, "linkedin")}
                                    type="text"/></td>
							</tr>
                            </tbody>
                            <tbody>

							<tr>
								<td><label className="label_profile"><span>Upload CV: </span></label></td>
								<td> <input className="body_relative ip_profile" type="file" onChange={this.handleChangeFile.bind(this)} />

                                </td>
							</tr>
                            </tbody>
                            <div/>
                            {/*<button className='cancel_but_profile' onClick={this.props.closePopup}>Cancel</button>*/}
                            {/*<button className="save_but_profile" type="submit" onClick={this.closePopup.bind(this)}>Save</button>*/}

						</table>
                        {
                            this.state.statusCV == 'upload_cv_success'?
                                <span className="cv_success">Upload success</span>
                                : null
                        }
                        {this.state.error.status ?
                                <div className="text-danger error_message">{this.state.error.message}</div>
                            : null
                        }
                        <div className="footer_bio">
                            <button className="btn_bio" type="submit" onClick={this.closePopup.bind(this)}>SAVE</button>
                        </div>

					</form>

				</div>
        );
    }

    render() {
        return (
			<div>
				{this.renderPopup()}
			</div>

        );
    }
}
export default UserPopup;



