import React from "react";
import {Link, withRouter} from "react-router-dom";
import GoogleSearch from "google-search";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import DropdownMenu from "./elements/DropdownMenu.jsx";
import LocalStorageService from "../services/LocalStorageService";
import CustomerStore from "../stores/CustomerStore";
import * as CustomerActions from "../actions/CustomerActions";
import * as properties from "../../properties";

@withRouter
export default class MenuComponent extends React.Component {
	constructor() {
		super();
		this.state = {
		};
	}

	componentWillMount() {
	}

	componentWillUnmount() {
	}

	componentDidMount() {
	}

	render() {
		return (
			<Tabs defaultIndex={0}>
				<div className="main_sidebar">
					<TabList className="ul_tab">
						<Tab className="li_tab"><i className="tabsli"><a className="tabsa"><Link to="/">MY PROFILE</Link></a></i></Tab><br/>
						<Tab className="li_tab"><i className="tabsli"><a className="tabsa"><Link to="/team">TEAM</Link></a></i></Tab><br/>
						<Tab className="li_tab"><i className="tabsli"><a className="tabsa"><Link to="/projects">PROJECTS</Link></a></i></Tab><br/>
					</TabList>
				</div>
			</Tabs>
		);
	}
}



