import React, {Component} from 'react';
import ReactTable from 'react-table';
import 'react-table/react-table.css'
import {projectDetail} from '../../actions/UserProfileAction';
import SeeProjects from './SeeProjects.jsx';
import ProjectStore from '../../stores/ProjectStore';
import originalMoment from "moment";
import {extendMoment } from "moment-range";

const moment = extendMoment(originalMoment);

class Team extends React.Component{
    render(){
        var teamProjects = [];
        this.props.teams.map( (el)=> {
            teamProjects.push(el.name);
        });
        var numTeams = teamProjects.length;
        var otherTeams = 0;
        if (numTeams > 5) {
            otherTeams = numTeams - 5;
        }
        return(
            <div className="team_pro">
                {this.props.teams.slice(0,5).map((el, index) => (
                    <span key={index} className="manaty_list">
                        <span>{el.fullName}</span>
                    </span>
                ))}
            </div>
        )
    }
}
class SkillDetail extends React.Component {
    render() {

        var photo64=this.props.logoBase64;
        if (photo64 != null){
           var photoDisplay=photo64;
        } else{
            var photoDisplay='';
        }
        var dateEnd= moment(this.props.dateEnd, "YYYY/MM/DD").format("MMM YYYY").toString();
        var dateStart= moment(this.props.dateStart, "YYYY/MM/DD").format("MMM YYYY").toString();
        if (dateStart === 'Invalid date') {
            dateStart = 'Ongoing';
        }
        if (dateEnd === 'Invalid date') {
            dateEnd = 'Ongoing';
        }

        var teamProjects = [];
        this.props.teams.map( (el)=> {
            teamProjects.push(el.name);
        });
        var numTeams = teamProjects.length;
        var otherTeams = 0;
        if (numTeams > 5) {
            otherTeams = numTeams - 5;
        }

        return (
            <div className="project_detail">

                <div className="div1_project">
                    <div className="picture_pro"><img src={"data:image/jpeg;base64," + photoDisplay} /></div>

                    <div className="out_pro">
                        <div className="all_pro">
                            <div className="name_li">
                                <span className="squa">[</span>
                                {this.props.name}&nbsp;
                                <span className="squa">]</span>
                            </div>
                            <div className="client_li">{this.props.client}&nbsp;</div>
                            {dateStart != dateEnd ?
                                    <div className="date_li"> {dateStart} - {dateEnd}&nbsp;</div>
                                : <div className="date_li"> {dateStart}&nbsp;</div>
                            }
                        </div>
                    </div>
                </div>
                <div className="since_project">
                    <div className="sin_in">{this.props.shortDescription}&nbsp;</div>
                </div>

                <div className="link_project">
                     {this.props.link ?
                         <div className="link_in"><a target="_blank" href={this.props.link}>{this.props.link}</a></div>
                         : <div className="link_inno">No link attached</div>
                     }
                </div>

                <div className="div3_project">
                    <div className="sub_pro">MANATY TEAM</div>
                    <div className="project_in">
                        <Team teams={this.props.teams} />
                        {
                            otherTeams > 0 ? <div className="manaty_list_see" onClick={() =>this.props.viewProject(this.props.code)}>And {otherTeams} More</div> : ''
                        }
                    </div>
                </div>

                <div className="div4_project">
                    <div className="btn_see" onClick={() =>this.props.viewProject(this.props.code)}>See project</div>
                </div>

            </div>
        );
    }
}

class UserProjectInfo extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showFrozen:false,
            amount: 0,
            users:[],
            email: "",
            map:null,
            countryDisplay:"",
            toLowerCase:"",
            render: false,
            projectSee: null,
            allUsers:[],
            fieldSearch:'',
            class1:'list',
            class2:'vig1',
            tabDisplay:true,
            dateStartChange:'',
            dateEndChange:'',
            clientChange:''
        };

        this.getCountryByCode = this.getCountryByCode.bind(this);
        this.viewProject = this.viewProject.bind(this);
    }

    componentWillMount() {
        ProjectStore.bindUpdateHandler(this.projectDetailOnUpdate.bind(this));

        /********Search********/
        var allProject = this.props.projectInfo;
        this.setState({
            allProject,
            projectSearch: allProject
        })
    }

    componentWillUnmount() {
        ProjectStore.unbindUpdateHandler(this.projectDetailOnUpdate.bind(this));
    }

    componentDidMount(){
    }

    componentDidUpdate(oldProps) {
        const newProps = this.props
        if(oldProps.projectInfo !== newProps.projectInfo) {
            this.setState({
                projectSearch:this.props.projectInfo
            })
        }
    }

    shouldComponentUpdate(nextProps, nextState){
        return true;
    }

    componentWillReceiveProps() {
    }

    projectDetailOnUpdate(response) {
        console.log("project On Update");
        console.log(response);
        const {result, message} = response
        if( message == "project_detail_success") {
            this.setState({
                projectSee: result
            });
        }
        this.state;
    }

    viewProject(name){
        projectDetail(name);
    }

    getCountryByCode(code){
        if(code != null){
            const {all_countries} = this.props.baseData;
            var _country = all_countries.filter(function (c) {
                return c.value.toLowerCase() == code.toLowerCase();
            });
            return _country[0].label;
        }
    }

    renderSkills(object){
        if (object) {
        var skill = object.map(function (val) {
            return (<div>{val.name}</div>);
        });
        return skill;
    }}

    viewProfile(name){
        projectDetail(name);
    }

    renderViewProject(){
        return(
            <div>
                <SeeProjects parent={this} users={this.props.users} backAllProjects={this.props.backAllProjects} resetFilter={this.resetFilter} projectSee={this.state.projectSee} {...this.state} baseData={this.props.baseData} {...this.props}/>
            </div>
        );
    }
    /********SEARCH********/
    searchDateStart(event) {
        const allProject = this.props.projectInfo;
        if (allProject != null){
                let searchInput = event.target.value,
                    displayedContacts = allProject.filter((el) => {
                        let searchValue = moment(el.dateStart, "YYYY/MM/DD").format("YYYY").toString() != null ?
                            moment(el.dateStart, "YYYY/MM/DD").format("MMMM-YYYY").toString().toLowerCase() : '';
                        let searchValueDateEnd = moment(el.dateEnd, "YYYY/MM/DD").format("YYYY").toString() != null ?
                            moment(el.dateEnd, "YYYY/MM/DD").format("MMMM-YYYY").toString().toLowerCase() : '';
                        let searchValueClient = el.client != null ? el.client.toLowerCase() : '';

                        return (searchValue.indexOf(searchInput.toLowerCase()) !== -1 ||
                                searchValueDateEnd.indexOf(searchInput.toLowerCase()) !== -1) &&
                                searchValueClient.indexOf(this.state.clientChange.toLowerCase()) !== -1;
                    })
                this.setState({
                    dateStartChange:searchInput,
                    dateEndChange:searchInput,
                    projectSearch: displayedContacts
                })
        }
    }

    searchClient(event) {
        const allProject = this.props.projectInfo;
        if (allProject != null){
            let searchInput = event.target.value,
                displayedContacts = allProject.filter((el) => {
                    let searchValue = el.client != null ? el.client.toLowerCase() : '';
                    let searchValueDateStart = moment(el.dateStart, "YYYY/MM/DD").format("YYYY").toString() != null ?
                        moment(el.dateStart, "YYYY/MM/DD").format("MMMM-YYYY").toString().toLowerCase() : '';
                    let searchValueDateEnd = moment(el.dateEnd, "YYYY/MM/DD").format("YYYY").toString() != '' ?
                        moment(el.dateEnd, "YYYY/MM/DD").format("MMMM-YYYY").toString().toLowerCase() : '';

                    return searchValue.indexOf(searchInput.toLowerCase()) !== -1 &&
                        (searchValueDateStart.indexOf(this.state.dateStartChange.toLowerCase()) !== -1 ||
                        searchValueDateEnd.indexOf(this.state.dateEndChange.toLowerCase()) !== -1);
                })
            this.setState({
                clientChange:searchInput,
                projectSearch: displayedContacts
            })
        }
    }

    searchAll(event) {
        const allProject = this.props.projectInfo;
        if (allProject != null){
            let searchInput = event.target.value,
                displayedContacts = allProject.filter((el) => {
                    let searchValueClient = el.client != null ? el.client.toLowerCase() : '';
                    let searchValueDateStart = moment(el.dateStart, "YYYY/MM/DD").format("MMMM-YYYY").toString() != null ?
                        moment(el.dateStart, "YYYY/MM/DD").format("MMMM-YYYY").toString().toLowerCase() : '';
                    let searchValueDateEnd = moment(el.dateEnd, "YYYY/MM/DD").format("MMMM-YYYY").toString() != null ?
                        moment(el.dateEnd, "YYYY/MM/DD").format("MMMM-YYYY").toString().toLowerCase() : '';
                    let searchValueLink = el.link != null ? el.link.toLowerCase() : '';
                    let searchValueName = el.name != null ? el.name.toLowerCase() : '';
                    let searchValueDescription = el.shortDescription != null ? el.shortDescription.toLowerCase() : '';

                    return (searchValueClient.indexOf(searchInput.toLowerCase()) !== -1 ||
                            searchValueDateStart.indexOf(searchInput.toLowerCase()) !== -1 ||
                            searchValueDateEnd.indexOf(searchInput.toLowerCase()) !== -1 ||
                            searchValueLink.indexOf(searchInput.toLowerCase()) !== -1 ||
                        searchValueName.indexOf(searchInput.toLowerCase()) !== -1 ||
                        searchValueDescription.indexOf(searchInput.toLowerCase()) !== -1 ||
                        el.teams.some(e => {
                            let searchValueTeams = e.fullName != null ? e.fullName.toLowerCase() : "";
                            return (
                                searchValueTeams.indexOf(searchInput.toLowerCase()) !==
                                -1
                            );
                        })
                    );
                })
            this.setState({
                projectSearch: displayedContacts
            })
        }
    }

    onChange(event) {
        this.setState({ fieldSearch: event.target.value });
    }

    renderManatyTeam(object){
        if (object) {
            var teams = object.map(function (val) {
                return (<div>{val.fullName}</div>);
            });
            return teams;
        }}

    renderProjectVig(){
        if (this.state.projectSearch) {
            var allProject = this.state.projectSearch;

            var detailTags = allProject.map((e, index) => (
            <SkillDetail
                code={e.code}
                name={e.name}
                client={e.client}
                dateStart= {e.dateStart}
                dateEnd= {e.dateEnd}
                shortDescription={e.shortDescription}
                link={e.link}
                viewProject={this.viewProject}
                view_log={this.view_log}
                getCountryByCode={this.getCountryByCode}
                teams={e.teams}
                logo={e.logo}
                logoBase64={e.logoBase64}
                assemblaLink={e.assemblaLink}
            />
            ));
        }

        return(
            <div className = "project_list" id="hidingScrollBar">
                <div class = "hideScrollBar">
                    {detailTags}
                </div>
                <div className="clear" />
            </div>
        );
    }

    renderDate(start, end) {
        var dateStart= moment(start, "YYYY/MM/DD").format("MMM YYYY").toString();
        var dateEnd= moment(end, "YYYY/MM/DD").format("MMM YYYY").toString();
        if (dateStart === 'Invalid date') {
            dateStart = 'Ongoing';
        }
        if (dateEnd === 'Invalid date') {
            dateEnd = 'Ongoing';
        }
        if (dateStart != dateEnd){
            return dateStart.concat(' - ', dateEnd);
        } else {
            return dateStart;
        }
    }

    renderProjectList(){
        const allProject = this.state.projectSearch;

        const columns = [{
            Header: <span>Project Name <i className='wd-sort-icon'/></span>,
            accessor: 'name'
        }, {
            Header: <span>Client <i className='wd-sort-icon'/></span>,
            accessor: 'client'

        }, {
            Header: 'Date',
            accessor: 'dateStart',
            Cell: props =>
                this.renderDate(props.original.dateStart,props.original.dateEnd)

        }, {
            Header: <span>Short description <i className='wd-sort-icon'/></span>,
            accessor: 'shortDescription',
            width: 300

        }, {
            Header: <span>Link <i className='wd-sort-icon'/></span>,
            accessor: 'link'

        }, {
            Header: <span>Manaty team <i className='wd-sort-icon'/></span>,
            accessor: 'teams',
            Cell: props => (
                this.renderManatyTeam(props.row.teams)
            )

        }, {
            Header:<span>SEE PROJECT</span>,
            accessor: 'code',
            Cell: props => (
                <button
                    style={{
                        backgroundColor: '#1d9c9c',
                        color:'white',
                    }}
                    onClick={() =>this.viewProject(props.row.code)}
                >See project</button>
            )
        }];

        return(
            <div className="project_list">
                <div class="hideScrollBar">
                    <div className="team-react-table">
                        <ReactTable className="-striped -highlight"
                            data={allProject}
                            columns={columns}
                            showPagination= {false}
                            showPageSizeOptions= {true}
                            pageSizeOptions= {[5, 10, 20, 25, 50, 100]}
                            defaultPageSize= {20}
                            showPageJump= {true}
                            sortable= {true}
                            resizable= {true}
                            minRows={3}
                            style={{
                                height: "100%" // This will force the table body to overflow and scroll, since there is not enough room
                            }}
                        />
                    </div>
                </div>
            </div>
        );
    }

    tabChanged(event){
        event.preventDefault();
        const state = this.state;
        state['showFrozen']= false
        this.setState(state);
    }

    handleClick1(){
        if (this.state.class1 == 'list' && this.state.class2 == 'vig1' && this.state.tabDisplay == true ){
            this.setState({
                class1: 'list1',
                class2: 'vig',
                tabDisplay:false
            });
        }
    }

    handleClick2(){
        if (this.state.class1 == 'list1' && this.state.class2 == 'vig'&& this.state.tabDisplay == false ){
            this.setState({
                class1: 'list',
                class2: 'vig1',
                tabDisplay: true
            });
        }
    }

    resetFilter(){
        this.setState({
            projectSearch: this.props.projectInfo,
            dateStartChange:'',
            dateEndChange:'',
            clientChange:''
        });
    }

    renderProject(){
        const users=this.props.projectInfo;
        const uniqueDateStart =[];
        const uniqueDateEnd =[];
        if (users) {
            users.map(item => uniqueDateStart.push(moment(item.dateStart, "YYYY/MM/DD").format("YYYY").toString()));
            users.map(item => uniqueDateEnd.push(moment(item.dateEnd, "YYYY/MM/DD").format("YYYY").toString()));
        }

        const uniqueDate = uniqueDateStart.concat(uniqueDateEnd);
        const uniqueDateSet  = [...new Set(uniqueDate.map(item => item))];

        const optionDates = uniqueDateSet.filter(el=> {
            return el != "" && el != null ;
        });
        const uniqueDateSelect = [];
        optionDates.map( function(item) {
             if (item === 'Invalid date') {
                item = 'Ongoing';
             }
             uniqueDateSelect.push(item);
        })
        uniqueDateSelect.sort();


        if(users) {
            const uniqueClient = [...new Set(users.map(item => item.client))];
            var uniqueClientSelect = uniqueClient.filter(el => {
                return el != "" && el != null;
            });

        uniqueClientSelect.sort();
        }

        return(
            <div className="row">
                <div className="col-xs-12 boder_header">
                    <div className="name_header">PROJECTS</div>
                </div>

                <div className="col-xs-12 filter_team">
                    <span className={this.state.class1} onClick={this.handleClick1.bind(this)}></span>
                    <span className={this.state.class2} onClick={this.handleClick2.bind(this)}></span>

                        <form>
                            <span><span className="filter_by">Filter by</span>
                                <select className="select_country" onChange={this.searchDateStart.bind(this)}>
                                        <option value="">Date</option>
                                        <option value="">All</option>
                                    {
                                        uniqueDateSelect!= null ?
                                            uniqueDateSelect.map((el) =>{
                                                return <option value={(el !== 'Ongoing' ? el: 'Invalid date' )}>{el}</option>;
                                            })
                                            : null
                                    }
                                 </select>

                                <select className="select_country" onChange={this.searchClient.bind(this)}>
                                        <option value="">Client</option>
                                        <option value="">All</option>
                                    {
                                        uniqueClientSelect!= null ?
                                            uniqueClientSelect.map((el) =>{
                                                return <option value={el}>{el}</option>;
                                            })
                                            : null
                                    }
                                 </select>
                            </span>
                            <button className="cancel_filter" onClick={this.resetFilter.bind(this)} type="reset">Cancel all filters</button>
                            <span className="relative_te"><input className="input_search" type='string' placeholder="Search" onChange={this.searchAll.bind(this)}/><span className="search_pro"></span></span>
                            </form>
                </div>

                <div className="col-xs-12 project_page">
                    {
                        this.state.tabDisplay == true ?
                            this.renderProjectVig()
                            : this.renderProjectList()
                    }
                </div>
            </div>
        )
    }

    render() {
        return (
            <div className="container-fluid project_team">
                {
                    this.props.projectSee == null ? this.renderProject() : this.renderViewProject()
                }
            </div>
        );
    }
}
export default UserProjectInfo;


